.header {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 60px;
  background: #00ADAF;
}

.ad {
  position: fixed;
  top: 50%;
  right: 0px;
  width: 150px;
  height: 150px;
  font-size: 12px;
  font-weight: 500;
  color: #333;
  z-index: 1;
}

.ad .adText {
  font-size: 10px;
  color: #333;
  opacity: 0.6;
}

.ad img {
  width: 150px;
  height: 100px;
}

.content {
  position: absolute;
  color: black;
  top: 70px;
  left: calc(50% - 500px);
  right: calc(50% - 500px);
  width: 1000px;
}